.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
};

@font-face {
  font-family: 'BalooThambi2';
  src: local('BalooThambi2'), url(/static/media/BalooThambi2-Regular.ee8debc9.ttf) format('truetype');
}

@font-face {
    font-family: 'BalooThambi2-Bold';
    font-weight: 900;
    src: local('BalooThambi2'), url(/static/media/BalooThambi2-Bold.c9951e0d.ttf) format('truetype');
}

@font-face {
    font-family: 'BalooThambi2-Medium';
    font-weight: 900;
    src: local('BalloThambi2'), url(/static/media/BalooThambi2-Medium.a050fa41.ttf) format('truetype');
}
:root {
  --dark: #374151;
  --darker: #1f2937;
  --darkest: #111827;
  --grey: #6b7280;
  --pink: #ec4899;
  --purple: #f5c9f0;
  --light: #eee;
}

* {
  font-family: BalooThambi2-Bold;
}

.Header {
  width: 100%;
  height: auto;
  margin-top: 2%;
  padding-top: 1%;
}

.Image {
  margin-top: 5%;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  justify-content: center;
}

.Name {
  margin-top: 40px;
  height: auto;
  text-align: center;
  font-weight: bold;
  font-size: x-large;
  margin-bottom: -1.5rem;
}

.Biography {
  margin-top: 40px;
  height: auto;
  text-align: center;
  font-weight: bold;
  font-size: medium;
  vertical-align: bottom;
  width: 100%;
  white-space: pre-line;
}

.Avatar {
  margin: 0 auto;
  width: 150px;
  height: 150px;
  text-align: center;
  margin-bottom: -1rem;
}

.social-platform-list {
  padding: 1rem;
  border-radius: 2rem;
  margin-bottom: 1rem;
  padding-top: 1rem;
}

.icon {
  height: 100%;
  margin: 0.5em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.title {
  width: auto;
  height: 100%;
  font-weight: bold;
  display: inline-block;
  margin: 0 auto;
}

.url {
  text-decoration: none;
}

.link {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.Body {
  width: 40%;
  height: auto;
  align-items: center;
  margin: 0 auto;
  flex-direction: column;
  justify-content: center;
}

::-webkit-scrollbar{
  width: 25px ;
}

/* Mobile: width < 714px */
@media only screen and (max-width: 44.625rem) {
  .Body {
    width: 70%;
    height: 100%;
  }
}

.error {
    color: gray;
    align-items: center;
    text-align: center;
}    
.animation {
    color: red;
    width: 50%;
    height: 50%;
    margin: 0 auto;
}
